.header {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: white;

  position: fixed;
  top: 0;
  width: 100%;
}

.header {
  background-color: rgb(250, 246, 246);
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Trebuchet MS", sans-serif;
  background-color: yellow;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  padding-left: 30px;
}

.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

.footer {
  background-image: url(Pictures/footer.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  margin-top:126px;
}

.tablearrangement {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 100px 10%;
  margin-top: 60px;
  margin-left:130px;
}

.pad {
  padding-right: 30px;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

.button {
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 250px;
  align-self: center;
}

.button:hover {
  background-color: #1b2331;
}

.topbutton {
  margin-left: 88px;
  margin-right: 88px;
}

.imagefamily {
  width: 400px;
  height: 300px;
  margin: 50px auto 0; /* Adds space at the top and centers the image */
  display: block;
}

.title {
  text-align: center;
}

.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px; /* Adjust spacing between logo and text */
}

.Link{
	text-decoration:none;
	color:black;
}

.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttons:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttons:active {
  background-color: #222;
  transform: scale(1);
}