    color: #000;
}

.addaccountcontainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #f9f9f9;
  gap: 20px; 
}

.accountinfo {
  display: flex;
  align-items: center;
  gap: 15px;
}

.text h2 {
  font-size: 18px;
  font-weight: 600;
}

.text p {
  font-size: 14px;
  color: #777;
}


.genderselection {
  font-family: Arial, sans-serif;
  max-width: 200px;
}

.radiooption {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: white;
  border: 1px solid #ddd;
  cursor: pointer;
}

.radiooption:first-of-type {
  border-top: 1px solid #ddd;
}

.radiooption input[type="radio"] {
  display: none;
}

.radiooption span {
  font-size: 14px;
  color: #333;
}

.radiooption input[type="radio"]:checked + span::before {
  background-color: #007bff; /* Customize color */
  border-color: #007bff;
}

.radiooption span::before {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 10px;
  border: 2px solid #ccc;
  border-radius: 50%;
  background-color: #f1f1f1;
}

.radiooption input[type="radio"]:checked + span::before {
  background-color: black;
  border: 2px solid black;
}

.radiooptions {
  display: inline-flex;
  gap: 10px; 
}

.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px; /* Adjust spacing between logo and text */
}

.Link{
	text-decoration:none;
	color:black;
}

.buttonse {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttonse:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttonse:active {
  background-color: #222;
  transform: scale(1);
}