.header {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: white;

  position: fixed;
  top: 0;
  width: 100%;
}

.header {
  background-color: rgb(250, 246, 246);
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding: 10px 0px; */
  font-family: "Trebuchet MS", sans-serif;
  background-color: yellow;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
}

.table {
  width: 65%; /* Set the width of the table (adjust as needed) */
  margin: 0 auto; /* Center the table horizontally */
  border-collapse: collapse; /* Make sure the table borders collapse into one */
}

.td {
  padding: 20px; /* Add padding inside each cell */
  vertical-align: middle; /* Vertically align content to the middle */
  text-align: center; /* Horizontally center text */
}

.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

.form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: auto;
}

.label {
  margin-top: 10px;
  font-weight: bold;
}

.textInput {
  margin-top: 5px;
  padding: 8px;
  font-size: 16px;
}

.fileInput {
  margin-top: 5px;
  padding: 8px;
  font-size: 16px;
}

.textareas{
  margin-top: 5px;
  padding: 8px;
  font-size: 16px;
}
.button {
  margin-top: 5px;
  padding: 8px;
  font-size: 16px;
}

.button {
  margin-top: 25px;
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 250px;
  align-self: center;
}

.button:hover {
  background-color: #1b2331;
}

.footer {
  background-image: url(Pictures/footer.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  margin-top: 20px;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

.spacetop {
  margin-top: 130px;
}

.tableText {
  width: 50%; /* Set the width of the left column */
}

.tableImage {
  width: 100%; /* Make the image take up 100% of the cell's width */
  max-width: 400px; /* Limit the max size of the image */
  height: auto; /* Maintain aspect ratio */
}

.normaltext {
  color: black;
  text-align: justify;
}

.label {
  color: black;
  text-align: justify;
  font-size: 12px;
}

.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px; /* Adjust spacing between logo and text */
}

.pad {
  padding-right: 30px;
}

.Link{
	text-decoration:none;
	color:black;
}

.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttons:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttons:active {
  background-color: #222;
  transform: scale(1);
}