.body {
    display: flex;
    justify-content: center;
    align-items: center;
	height:643px;
}

.pad {
  padding-right: 30px;
}

.header {
  background-image: url(Pictures/header.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
}

.header {
  background-color: rgb(250, 246, 246);
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Trebuchet MS", sans-serif;
  background-color: yellow;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  padding-left: 30px;

}

.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

.loginleft {
    flex: 1;
    padding: 40px;
    text-align: center;
	margin-top:-20px;
}

.loginleft h1 {
    font-size: 2em;
    font-weight: bold;
}

.loginleft p {
    margin-top: 10px;
    font-size: 1em;
    color: #666;
}

.container {
    display: flex;
    width: 100%;
    max-width: 100vh;
	min-height: 200px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    align-items: center;
	margin-top:200px;
	
}

.loginright {
    flex: 1;
    padding: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
	margin-right:100px;
	margin-top:0px;
}

.form label {
    display: block;
    margin: 15px 0 5px;
    font-weight: bold;
}

.accounttype {
    display: flex;
    gap: 10px;
}

.accountbtn:hover,
.accountbtn:focus {
    background-color: #ddd;
	color: black;
}

.accountbtn {
  padding: 10px 20px;
  margin: 5px;
  border: 1px solid #ccc;
  background-color: #f5f5f5;
  color: black;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease, color 0.3s ease;
}


.accountbtn.selected {
  background-color: #007bff; 
  color: white;
  border-color: #0056b3;
}

.accountbtn.selected:hover {
  background-color: #0056b3; 
  color: white;
}


.input{
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-bottom: 15px;
    font-size: 1em;
}

.buttons {
    display: flex;
    gap: 10px;
    margin-top: 20px;
}

.cancelbtn,
.loginbtn {
    margin-top: 10px;
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 190px;
  align-self: center;
}

.cancelbtn {
    background-color: #000;
    border: 1px solid #000;
    color: #fff;
}

.loginbtn {
    background-color: #000;
    color: #fff;
    border: none;
}

.cancelbtn:hover {
  background-color: #1b2331;
}

.loginbtn:hover {
	background-color: #1b2331;
}

.footer {
  background-image: url(Pictures/footer.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  margin-top:207px;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px;
}

.Link{
  text-decoration:none;
  color:black;
}

.buttonsa {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttonsa:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttonsa:active {
  background-color: #222;
  transform: scale(1);
}
