.repheader {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: white;

  position: fixed;
  top: 0;
  width: 100%;
}
.pad {
  padding-right: 30px;
}
.repheader {
  background-color: rgb(250, 246, 246);
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding: 10px 0px; */
  font-family: "Trebuchet MS", sans-serif;
  background-color: yellow;
}

.repheader .logo {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
}


.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

.repcontainer{
	height:367px;
	width:70%;
	margin-left:15%;
	margin-right:15%;
    background-color: #ffffff;
	margin-top:-4%;
	
}
.repcontainer h1{
	text-align:center;
	margin-top:140px;
}

.repcontainer form{
	text-align:center;
}

.form button{
	background-color: black;
}

.label{
	padding-bottom:10px;
}
.replist{
	max-height:90%;
	overflow-y:scroll;
	font-size:20px;
	border:1px solid black;
}	
.repcontainer table{
	width:100%;
	border-collapse:collapse
}
.td {
	text-align:center;
}
.description{
	width:80%;
	border:solid black;
	height:50px;
}
.status{
	border:solid black;
  width:10%;
}

.reportid{
	width:10%;
	border:solid black;
}

.listdescription{
	text-align:left;
	width:40%;

}
.listreportid{
	width:40%;
}
.liststatus{
	width:20%;;
}

.unbanline button{
	float:right;
	position:relative;
}	
.lock{
	height:50px;
	width:50px;
}
.footer {
  background-image: url(Pictures/footer.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 0px;
  margin-top: 100px;
  position:absolute;
  bottom:0px;
  width: 100%;

}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}
.rephead{
	margin-bottom:10px;
}
.statusfilter{
	margin-top: 10px;
	margin-bottom: 10px;
	padding: 10px;
	color: black;
	border: 1px solid black;
	cursor: pointer;
	width: 250px;
	float:right
}

.replogo {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px; /* Adjust spacing between logo and text */
}

.repsearch{
  height:30px;
  width:80%;
}
.clickablerow:hover {
  background-color: #f2f2f2; /* Light grey background on hover */
  border:1px solid black;
}
.clickablerow td{
  border:1px solid black;
}


.Link{
	text-decoration:none;
	color:black;
}

.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttons:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttons:active {
  background-color: #222;
  transform: scale(1);
}